import React, {useState} from "react";
import s from "./Paginator.module.css";

let Paginator = ({currentPage, totalItemsCount, pageSize, onPageChanged, portionSize = 10}) => {
	
	let pagesCount = Math.ceil(totalItemsCount / pageSize);
	
	let pages = [];
	for (let i = 1; i <= pagesCount; i++) {
		pages.push(i);
	}
	
	let portionCount = Math.ceil(pagesCount / portionSize);
	let [portionNumber, setPortionNumber] = useState(1);
	let leftPortionPageNumber = (portionNumber - 1) * portionSize +1;
	let rightPortionNumber = portionNumber * portionSize;
	
	return (
	  <div className={s.paginator}>
		  {portionNumber > 1 &&
		  <span>
			  <button className={s.pag} onClick={() => setPortionNumber(1)}>begin</button>
			  <button onClick={() => setPortionNumber(portionNumber - 1)}>&#8592;</button>
		  </span>}
		  
		  {pages
		  .filter(p => p >= leftPortionPageNumber && p <= rightPortionNumber)
		  .map(p => {
			  return <span className={currentPage === p && s.selectedPage} key={p.id}
								onClick={(e) => {
									onPageChanged(p);
								}}>{p}</span>
		  })}
		  
		  {portionCount > portionNumber &&
		  <span>
			  <button onClick={() => setPortionNumber(portionNumber + 1)}>&#8594;</button>
			  <button className={s.pag} onClick={() => setPortionNumber(portionCount)}>end</button>
			 </span>}
	  </div>
	)
};

export default Paginator;
