const SEND_MESSAGE = 'SEND_MESSAGE';

let initialState = {
	dialogs: [
		{id: 1, name: 'Vasya', ava: '1'},
		{id: 2, name: 'Petya', ava: '2'},
		{id: 3, name: 'Ignat', ava: '3'},
		{id: 4, name: 'Alex', ava: '4'},
		{id: 5, name: 'Sveta', ava: '5'},
		{id: 6, name: 'Valera', ava: '6'},
	],
	messages: [
		{id: 1, message: 'Hi!'},
		{id: 2, message: 'How are you?'},
		{id: 3, message: 'Yo', messageMe: 'Hi! I\'m fine!'},
		{id: 4, messageMe: 'How are you? Text Text Text Text Text Text Text Text'},
		{id: 5, message: 'Fine!', messageMe: 'It\'s good!'},
	]
};

const dialogsReducer = (state = initialState, action) => {
	
	switch (action.type) {
		case SEND_MESSAGE:
			let newMessage = {
				id: state.messages[state.messages.length - 1].id + 1,
				messageMe: action.newMessageBody
			};
			return {
				...state,
				messages: [...state.messages, newMessage]
			};
		default:
			return state;
	}
};

export const addMessageActionCreator = (newMessageBody) => ({type:SEND_MESSAGE, newMessageBody});

export default dialogsReducer;