import {profileApi, usersApi} from "../api/api";

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';
const DELETE_POST = 'DELETE_POST';

let initialState = {
	posts: [
		{id: 3, likesCounts: 15, message: 'Hi! How ara you?'},
		{id: 2, likesCounts: 20, message: 'It\'s my first post. It\'s my first post .It\'s my first post .It\'s my first post . It\'s my first post. It\'s my first post'},
		{id: 1, likesCounts: 1, message: 'Yooo, man!'},
	],
	profile: null,
	status: ''
};

const profileReducer = (state = initialState, action) => {
	switch (action.type) {
		case ADD_POST: {
			let newPost = {
				id: state.posts[0].id + 1,
				likesCounts: 0,
				message: action.newPostText
			};
			return {
				...state,
				posts: [newPost, ...state.posts]
			}
		}
		case SET_STATUS: {
			return {
				...state, status: action.status
			};
		}
		case SET_USER_PROFILE: {
			return {
				...state, profile: action.profile
			};
		}
		case DELETE_POST: {
			return {
				...state, posts: state.posts.filter(p => p.id !== action.postId)
			};
		}
		default:
			return state;
	}
};

export const addPostActionCreator = (newPostText) => ({type: ADD_POST, newPostText});
export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile}) ;
export const setStatus = (status) => ({type: SET_STATUS, status}) ;
export const deletePost = (postId) => ({type: DELETE_POST, postId}) ;

export const getUserProfile = (userId) => async (dispatch) => {
	let data = await usersApi.getProfile(userId);
	dispatch(setUserProfile(data));
};

export const getStatus = (userId) => async (dispatch) => {
	let response = await profileApi.getStatus(userId);
	dispatch(setStatus(response.data));
};

export const updateStatus = (status) => async (dispatch) => {
	let response = await profileApi.updateStatus(status)
	
	if (response.data.resultCode === 0) {
		dispatch(setStatus(status));
	}
};

export default profileReducer;