import React from "react";
import s from "./Login.module.css"
import style from "../common/FormsContols/FormsControls.module.css"
import {Field, reduxForm, reset} from "redux-form";
import {createField, Input} from "../common/FormsContols/FormsControls";
import {required} from "../../utils/validators/validators";
import {connect} from "react-redux";
import {login} from "../../redux/auth-reducer";
import {Redirect} from "react-router-dom";

const LoginForm = ({handleSubmit, error}) => {
	return (
	  <form onSubmit={handleSubmit}>
		  <div className={s.loginForm}>
			  {createField("Email", "email", [required], Input)}
			  {createField("Password", "password", [required], Input, "password")}
			  <div>
				  <Field component={"input"} name={"rememberMe"} type={"checkbox"} /> Remember me
			  </div>
			  { error && <div className={style.formSummeryError}>
				 {error}
			  </div> }
			  <div>
				  <button>Sign Up</button>
			  </div>
		  </div>
	  </form>
	)
};

const afterSubmit = (result, dispatch) =>
  dispatch(reset('login'));

const LoginReduxForm = reduxForm ({form: 'login', onSubmitSuccess: afterSubmit}) (LoginForm);

const Login = (props) => {
	const onSubmit = (formData) => {
		props.login(formData.email, formData.password, formData.rememberMe);
	};
	
	
	if(props.isAuth) {
		return <Redirect to={"/profile"} />
	}
	return (
	  <div className={s.login}>
		  <h2>Login</h2>
		 	<LoginReduxForm onSubmit={onSubmit}  />
	  </div>
	)
};

const mapStateToProps = (state) => ({
	isAuth: state.auth.isAuth
});

export default connect (mapStateToProps, {login}) (Login);